img{
  max-height: 100%;
  max-width: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: fill;
}

.centered{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}

.elementToFadeInAndOut {
  opacity: 1;
  transition: opacity 2s linear;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
  box-shadow: 0 0 100px rgba(0,0,0,0.5) inset;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#chat{
	padding-left:0;
	margin:0;
	list-style-type:none;
	height:25%;

}
#chat li{
	padding:10px 30px;
}
#chat h2,#chat h3{
	display:inline-block;
	font-size:16px;
	font-weight:normal;
}
#chat h3{
	color:#6fbced;
}
#chat .entete{
  padding-left: 5%;
  text-align: left;
	margin-bottom:5px;
}
#chat .message{
	padding:20px;
	color:#fff;
  font-size: 16px;
	line-height:25px;
	width:90%;
	display:inline-block;
	text-align:left;
	border-radius:20px;
}
#chat .me{
	text-align:right;
}
#chat .you .message{
	background-color:#6fbced;
}
#chat .me .message{
	background-color:#6fbced;
}
#chat .triangle{
	width: 0;
	height: 0;
	border-style: solid;
	border-width: 0 10px 10px 10px;
}
#chat .you .triangle{
		border-color: transparent transparent #6fbced transparent;
		margin-left:5%;
}
#chat .me .triangle{
		border-color: transparent transparent #6fbced transparent;
		margin-left:375px;
}

